}

/* Icon Colors */
.fa-blue {
	color: #0000ff;
}

.fa-gray {
	color: #9b9595;
}

.fa-green {
	color: #90af45;
}

.fa-orange {
	color: #ffa500;
}

.fa-purple {
	color: #820182;
}

.fa-red {
	color: #ff0000;
}

.fa {
	margin-right: 3px;
}

.edit-mode-btn {
	position: fixed;
	top: 5px;
	right: 5px;
	padding: 12px 15px;
	font-size: 18px;
	border-radius: 100%;
	opacity: 0.8;
	z-index: 1000;
}

.edit-mode-btn:hover {
	opacity: 0.6;
	transform: rotate(15deg);
}

/* Misc */
.field-label {
	font-weight: bold;
	margin-top: 5px;
	margin-right: 4px;
}

.label-hidden {
	visibility: hidden;
	height: 0;
}

.label-inline {
	display: inline-block;
}

.label-newline {
	display: block;
}

.topic-title {
	float: none;
	display: inline;
}

.sm-postbody,
.sm-block-container .postbody {
	width: 100%;
}

.sm-fill-space {
	overflow: hidden;
}

/* text alignment */
.align-right {
	text-align: right;
}

.align-center {
	text-align: center;
}

.align-left {
	text-align: left;
}

.sm-responsive-show {
	display: none;
	overflow: hidden;
}

.sm-responsive-hide {
	display: block;
}

@media screen and (max-width: 940px) {
	.sitemaker {
		position: relative;
	}
	.sm-responsive-show {
		display: block;
	}
	.sm-responsive-hide {
		display: none;
	}
	.sm-responsive-full-width {
		width: 100%;
	}
	.edit-mode-btn {
		padding: 7px 10px;
	}
}